@media (max-width: 767px) {
  .tableRowWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    color: black;
    border-bottom: 1px solid #B09C85; }
    .tableRowWrapper div {
      text-align: left;
      padding-top: 20px;
      padding-bottom: 20px; }
    .tableRowWrapper .rowTitle {
      text-align: left;
      width: 72%;
      padding-right: 10px; }
    .tableRowWrapper .rowPrice {
      text-align: right;
      width: 30%; }
    .tableRowWrapper .rowTax {
      display: none; }
    .tableRowWrapper .rowFee {
      display: none; }
    .tableRowWrapper:last-of-type {
      border-bottom: 0px solid #B09C85; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .tableRowWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
    font-size: 16px;
    color: black;
    border-bottom: 1px solid #B09C85; }
    .tableRowWrapper div {
      text-align: left;
      padding-top: 20px;
      padding-bottom: 20px; }
    .tableRowWrapper .rowTitle {
      text-align: left;
      width: 71%;
      padding-right: 10px; }
    .tableRowWrapper .rowPrice {
      padding-left: 30px;
      width: 16%; }
    .tableRowWrapper .rowTax {
      padding-left: 20px;
      width: 13%;
      text-align: right; }
    .tableRowWrapper:last-of-type {
      border-bottom: 0px solid #B09C85; } }

@media (min-width: 1024px) {
  .tableRowWrapper {
    display: flex;
    font-weight: 500;
    font-size: 22px;
    line-height: 26px;
    color: black;
    border-bottom: 1px solid #B09C85; }
    .tableRowWrapper div {
      text-align: right;
      padding-top: 28px;
      padding-bottom: 28px; }
    .tableRowWrapper .rowTitle {
      text-align: left;
      width: 71%;
      padding-right: 10px; }
    .tableRowWrapper .rowPrice {
      width: 11%; }
    .tableRowWrapper .rowTax {
      width: 18%;
      text-align: right; }
    .tableRowWrapper:last-of-type {
      border-bottom: 0px solid #B09C85; } }
