@media (max-width: 767px) {
  .serviceRowHeading {
    cursor: pointer;
    width: 100%;
    display: flex;
    position: relative;
    justify-content: flex-end;
    align-items: center;
    user-select: none; }
    .serviceRowHeading h1 {
      padding: 25px 0 27px;
      width: 100%;
      text-align: center;
      margin: 0 auto;
      color: #040404;
      font-size: 18px;
      line-height: 1.2;
      font-weight: 500; }
    .serviceRowHeading span {
      font-size: 29px;
      transition: 0.2s; }
      .serviceRowHeading span.rotate {
        transition: 0.2s;
        transform: rotate(45deg); } }

@media (min-width: 768px) {
  .serviceRowHeading {
    cursor: pointer;
    width: 100%;
    height: 88px;
    display: flex;
    position: relative;
    justify-content: flex-end;
    align-items: center;
    user-select: none; }
    .serviceRowHeading h1 {
      top: 31px;
      width: 100%;
      position: absolute;
      text-align: center;
      color: #040404;
      font-size: 28px;
      font-weight: 500; }
    .serviceRowHeading span {
      font-size: 42px;
      transition: 0.2s; }
      .serviceRowHeading span.rotate {
        transition: 0.2s;
        transform: rotate(45deg); } }
