.none {
  display: none; }

@media (max-width: 767px) {
  .serviceTableWrapper {
    width: 300px;
    margin: 50px auto 70px; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .serviceTableWrapper {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    width: 634px;
    margin: 49px auto 0; } }

@media (min-width: 1024px) and (max-width: 1439px) {
  .serviceTableWrapper {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    width: 924px;
    margin: 45px auto 0; } }

@media (min-width: 1440px) {
  .serviceTableWrapper {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    width: 1236px;
    margin: 45px auto 0; } }
